	module.exports = {"data":{"wordpressPost":{"title":"The NHS Health Check in England, bereavement by suicide, and public involvement in medical research: Most read articles in January","content":"<h1>NHS Health Checks, the risks of bereavement by suicide, and public involvement in medical research</h1>\n<p><img class=\"alignnone\" title=\"Image: USMC-100209-M-1998T-001.jpg, from Wikipedia, by United States Marine Corps\" src=\"https://upload.wikimedia.org/wikipedia/commons/thumb/7/7f/USMC-100209-M-1998T-001.jpg/800px-USMC-100209-M-1998T-001.jpg\" alt=\"File:USMC-100209-M-1998T-001.jpg\" width=\"354\" height=\"235\" /></p>\n<p>The Top 10 Most Read list for January is almost completely comprised of new entries, with the exception of the ever popular review by Kristensen <em>et al</em> on the effects of statins. In the top spot this month is an evaluation of the first four years of the NHS Health Check in England, by Robson<em> et al</em>. At number two we have a cross-sectional study seeking to test the hypothesis that young adults bereaved by suicide have an increased risk of suicidal ideation and suicide attempt compared to their peers bereaved by other sudden deaths. Parsons <em>et al</em> enter the list at number five with a paper asking what pharmaceutical industry professionals believe about public involvement in medicines research and development, and Joost <em>et al</em> enter at number eight with a paper on persistent spatial clusters of high body mass index.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Robson <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e008840.full\">The NHS Health Check in England: an evaluation of the first 4 years</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Pitman <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e009948.full\">Bereavement by suicide as a risk factor for suicide attempt: a cross-sectional national UK-wide study of 3432 young bereaved adults</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Kristensen <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/9/e007118.full\">The effect of statins on average survival in randomised trials, an analysis of end point postponement</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Carey <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/12/e009002.full\">Systems science and systems thinking for public health: a systematic review of the field</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Parsons <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e008928.full\">What do pharmaceutical industry professionals in Europe believe about involving patients and the public in research and development of medicines? A qualitative interview study</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Friis <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e009627.full\">Gaps in understanding health and engagement with healthcare providers across common long-term conditions: a population health survey of health literacy in 29 473 Danish citizens</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Ovseiko <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e009022.full\">Markers of achievement for assessing and monitoring gender equity in translational research organisations: a rationale and study protocol</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Joost <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e010145.full\">Persistent spatial clusters of high body mass index in a Swiss urban population as revealed by the 5-year GeoCoLaus longitudinal study</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Parsons <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e008473.full\">&#8216;Please don&#8217;t call me Mister&#8217;: patient preferences of how they are addressed and their knowledge of their treating medical team in an Australian hospital</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Greenhalgh <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e009388.full\">Virtual online consultations: advantages and limitations (VOCAL) study</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"d0fe4cd4-4abc-521d-b3c4-618f5a834127"}}

